.map-container {
    height: 500px;
    width: 100%;
    border-color: #bfa888;
    border-top-width: 2px;
    border-top-style: solid;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    float: left;
}

.leaflet-container {
    width: 100%;
    height: 500px;
    z-index: 1;
}
.leaflet-control-attribution.leaflet-control > a:first-child {
    display: none;
}
